// src/components/CreateGoalForm.tsx
import { useState } from 'react';

const CreateGoalForm = ({ onAddGoal }: { onAddGoal: Function }) => {
  const [title, setTitle] = useState('');
  const [totalAmount, setTotalAmount] = useState<number>(0);
  const [savedAmount, setSavedAmount] = useState<number>(0);
  const [targetDate, setTargetDate] = useState<string>('');

  const handleSubmit = (e: React.FormEvent) => {
	e.preventDefault();
	onAddGoal({ title, totalAmount, savedAmount, targetDate });
	setTitle('');
	setTotalAmount(0);
	setSavedAmount(0);
	setTargetDate('');
  };

  return (
	<form onSubmit={handleSubmit} className="space-y-4 p-4">
	  <input
		type="text"
		placeholder="Titre de l'objectif"
		value={title}
		onChange={(e) => setTitle(e.target.value)}
		className="p-2 border rounded"
	  />
	  <input
		type="number"
		placeholder="Montant total"
		value={totalAmount}
		onChange={(e) => setTotalAmount(Number(e.target.value))}
		className="p-2 border rounded"
	  />
	  <input
		type="number"
		placeholder="Montant économisé"
		value={savedAmount}
		onChange={(e) => setSavedAmount(Number(e.target.value))}
		className="p-2 border rounded"
	  />
	  <input
		type="date"
		value={targetDate}
		onChange={(e) => setTargetDate(e.target.value)}
		className="p-2 border rounded"
	  />
	  <button type="submit" className="p-2 bg-blue-500 text-white rounded">Ajouter</button>
	</form>
  );
};

export default CreateGoalForm;
